import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  CustomInput,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import IntlMessages from '../../../../helpers/IntlMessages';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import { adminRoot } from '../../../../constants/defaultValues';
import StateButton from '../../../../components/StateButton';

const handleSuccessButtonClick = () => {
  return new Promise((success) => {
    setTimeout(() => {
      success('Everything went right!');
    }, 1400);
  });
};

const Education = ({ match }) => {
  const [editEducation, setEditEducation] = useState(false);

  const [startDate, setStartDate] = useState(new Date());

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.education" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>
      <Row className="justify-content-center">
        <Colxx xxs="12" md="8">
          <Card className="mb-4">
            <div className="position-absolute card-top-buttons">
              <Button
                outline
                color="primary"
                onClick={() => {
                  setEditEducation(!editEducation);
                }}
                className="icon-button"
              >
                <i className="simple-icon-pencil" />
              </Button>
            </div>

            <CardBody>
              <CardTitle>
                <h2 className="lead">
                  <IntlMessages id="user.update-education" />
                </h2>
                <p className="mt-2">
                  <IntlMessages id="user.update-education-desc" />
                </p>
              </CardTitle>

              <Separator className="mb-3" />

              <FormGroup>
                <CustomInput
                  type="checkbox"
                  id="formal-education"
                  disabled={!editEducation}
                  label="I don't have Formal Education."
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.degree-type" />
                </Label>
                <Input
                  type="text"
                  name="degree-type"
                  placeholder="Degree Type"
                  disabled={!editEducation}
                />
              </FormGroup>

              <FormGroup className="mb-5">
                <Label>
                  <IntlMessages id="forms.select-institution" />
                </Label>
                <Input
                  type="text"
                  name="select-institution"
                  placeholder="e.g Vellore Institute of Technology"
                  disabled={!editEducation}
                />
              </FormGroup>

              <Separator className="mt-2 mb-2" />

              <FormGroup>
                <CustomInput
                  type="checkbox"
                  disabled={!editEducation}
                  id="curently-study-here"
                  label="I currently study here."
                />
              </FormGroup>

              <FormGroup className="mb-3">
                <Label>
                  <IntlMessages id="forms.field-of-study" />
                </Label>
                <Input
                  type="text"
                  name="field-of-study"
                  placeholder="e.g. Software Engineering"
                  disabled={!editEducation}
                />
              </FormGroup>
              <Row>
                <Colxx>
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.month-of-graduation" />
                    </Label>
                    {editEducation ? (
                      <DatePicker
                        selected={startDate}
                        onChange={(date) => setStartDate(date)}
                        dateFormat="MM"
                        showMonthYearPicker
                      />
                    ) : (
                      <Input
                        type="text"
                        name="month"
                        value="4"
                        disabled={!editEducation}
                      />
                    )}
                  </FormGroup>
                </Colxx>
                <Colxx>
                  <FormGroup className="mb-3">
                    <Label>
                      <IntlMessages id="forms.year-of-graduation" />
                    </Label>
                    {editEducation ? (
                      <DatePicker
                        selected={startDate}
                        onChange={(date) => setStartDate(date)}
                        showYearPicker
                        dateFormat="yyyy"
                        yearItemNumber={9}
                      />
                    ) : (
                      <Input
                        type="text"
                        name="year"
                        value="2025"
                        disabled={!editEducation}
                      />
                    )}
                  </FormGroup>
                </Colxx>
              </Row>

              {editEducation && (
                <FormGroup className="text-center">
                  <StateButton
                    id="submitAndNextButton"
                    color="primary"
                    onClick={handleSuccessButtonClick}
                    next={() => {
                      setEditEducation(true);
                    }}
                  >
                    <IntlMessages id="forms.submit" />
                  </StateButton>
                </FormGroup>
              )}
            </CardBody>
          </Card>
        </Colxx>
      </Row>
      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              disabled={editEducation}
              href={`${adminRoot}/profile/about`}
            >
              <IntlMessages id="forms.previous" />
            </Button>

            <Button
              color="primary"
              className="m-3"
              disabled={editEducation}
              href={`${adminRoot}/profile/experience`}
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};
export default Education;
